import React, {Component} from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import './PhotoSection.css'

export default class PhotoSection extends Component {
    render() {
        const {
            type,
            what,
            auto,
            srcDo,
            srcAfter,
            isOnePic
        } = this.props.val
        return (
            <div className='row mt-3'>
                <div className='col-lg-5 black-back d-flex align-items-center '>
                    <div className='conatiner'>
                        <div className='nadpis pt-4 pl-4'>
                            {type}
                            <span className='color2'>{what}
                            </span><br/>{auto}
                        </div>
                    </div>
                </div>

                <div
                    className={classNames('col-lg pt-3 justify-content-center', {
                    'col-12': isOnePic,
                    'col-6': !isOnePic
                })}>
                    <img src={srcDo} className=' rounded img-fluid' alt=''></img>
                </div>
                {!isOnePic
                    ? <div
                        className={classNames('col-lg pt-3  justify-content-center', {
                            'col-12': isOnePic,
                            'col-6': !isOnePic
                        })}>
                            <img src={srcAfter} className=' rounded img-fluid' alt=''></img>
                        </div>
                    : null}

            </div>
        )
    }
}
PhotoSection.propType = {
    val: PropTypes
        .shape({
        type: PropTypes.string.isRequired,
        what: PropTypes.string.isRequired,
        auto: PropTypes.string.isRequired,
        srcDo: PropTypes.string.isRequired,
        srcAfter: PropTypes.string,
        isOnePic: PropTypes.bool.isRequired
    })
        .isRequired
}



