import React, {Component} from 'react'
import LogoImage from '../../images/logo.png'
import './Logo.css'
export default class Logo extends Component {
    render() {
        return (
            <div className='col-12 col-lg' >
                <img src={LogoImage} className='img-fluid' alt='logo'></img>
            </div>
        )
    }
}



