import React, {Component} from 'react'
import PhotoSection from '../../components/Main/PhotoSection'
import {connect} from 'react-redux'
import {getWorkExamples} from '../../reducers/init/reducer'

export class Examples extends Component {
    render() {
        const {Examples} = this.props
        return (
            <div>
                {Examples.map((val, i) => (<PhotoSection key={i} val={val}/>))}
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => ({Examples: getWorkExamples(state)})

export default connect(mapStateToProps)(Examples)



