import React, { Component } from 'react'

export default class About extends Component {
    render() {
        return (
            <div className='col-lg col-12  d-flex align-items-center border-1px-black'>
                <p className='d-flex justify-content-center txtNew'>Приятно осознавать, что ты
                    двигаешься к цели каждый день, маленькими шажками, единичными действиями в
                            правильном направлении!<br /><br />
                    Нам важно не количество клиентов, а количество их улыбок! Каждая из которых
                    выражает приятное удивление.

                            <br /><br />
                    Спасибо вам за то, что вы цените чистоту! Ждем ваши улыбки в наших центрах)</p>
            </div>
        )
    }
}



