import React, {Component} from 'react'
import BlackBlock from '../../components/BlackBlock'
import logo3 from '../../images/logo3.jpeg'

import {NavLink, withRouter} from 'react-router-dom'

class Offers extends Component {
    render() {
        return (
            <div className='container mt-4'>
                <div className='row m-3 pl-3'>
                    <div className='col-lg col-12'>
                        <BlackBlock>
                            <div >
                                <h2>
                                    <span >МАССА</span>
                                    <span className='color2 '>ВЫГОДНЫХ</span>
                                </h2>
                                <h2>
                                    ПРЕДЛОЖЕНИЙ
                                </h2>
                            </div>
                        </BlackBlock>
                    </div>
                    <div className='col-lg-8 col-12 w-100 h-100'>
                        <img
                            src={logo3}
                            className='img-fluid'
                            style={{
                            width: '624px',
                            height: '295px',
                            objectFit: 'cover'
                        }}
                            alt=''/>
                    </div>
                </div>
                <div className='row mt-4'>
                    <div className='container p-5'>
                        <div className='borderr p-4'>
                            <h2>Сезонное предложение для истинных автоценителей</h2 >
                            <br/>
                            <div>
                                <h6 >Центр по уходу за автомобилем "Чистая тонна" предлагает воспользоваться
                                    комплексами услуг с невероятными скидками:<br/>
                                    <br/>
                                    Комплексная химчистка + Легкая восстановительная полировка со скидкой 20%!<br/>
                                    (5000 + 3500) - 15% = 7225(на примере автомобиля Класса 1)<br/>
                                    <br/>
                                    Остается дело за малым, выбрать дату преображения вашего автомобиля, а наши
                                    подарки будут раздаваться с 1 по 31 августа 2017 года!</h6>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='text-center mb-5'>
                    <NavLink to='/calc' className='nav-link' activeClassName='active'>Перейти к выбору</NavLink>
                </div>
            </div>
        )
    }
}

export default withRouter(Offers)


