import React, {Component} from 'react'
import Rewiew from '../../components/Main/Rewiew'

import {getRewiews} from '../../reducers/init/reducer'
import {connect} from 'react-redux'

export class Rewiews extends Component {
    render() {
        const {Rewiews} = this.props
        return (
            <div>
                {Rewiews.map((val, i, ar) => {
                    if ((i + 1) % 2) {
                        return (
                            <div key={i + 1} className='row mt-5'>
                                <Rewiew val={val}/> {ar[i + 1]
                                    ? <Rewiew val={ar[i + 1]}/>
                                    : null}
                            </div>
                        )
                    } else {
                        return null
                    }

                })}

            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => ({Rewiews: getRewiews(state)})

export default connect(mapStateToProps)(Rewiews)


