import React, {Component} from 'react'

export default class Lozung extends Component {
    render() {
        return (

            <div className='col-lg-3 col-12'>
                <div
                    className='h-100 p-2 black-back justify-content-center d-flex align-items-end'>
                    <strong className=' display-4 text-white '>
                        Чистота
                        <span className='color2'>в центре
                        </span>внимания</strong>
                </div>
            </div>
        )
    }
}



