import React, { Component } from 'react'
import foto1 from '../../images/pic1.jpg'

export default class Photo extends Component {
    render() {
        return (
            <div className='col-lg col-12'>
                <img className='img-fluid' src={foto1} alt=''></img>
            </div>
        )
    }
}



