import React, {Component} from 'react'
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import classNames from 'classnames'
import './ServiceMain.css'
import Photo from '../../images/serviceMain.jpg'
import {getServiceInfo} from '../../reducers/init/reducer'
import {platform} from '../../utils/platform';
export class ServiceMain extends Component {
    render() {
        const {Info: info} = this.props
        const isMobile = platform()
        return (

            <div
                className='m-3 mx-auto d-flex image container'
                style={!isMobile
                ? {
                    backgroundImage: `url(${Photo})`
                }
                : {
                    backgroundImage: `url(${Photo})`,
                    backgroundRepeat: 'no-repeat',
                    width: 'auto',
                    height: 'auto'
                }}>

                <div className='row mt-auto w-100  '>
                    {info.map((val, i) => <div className='col-lg-4 col-6'>
                        <div
                            className={classNames(' text', {
                            'h6 m-2': isMobile
                        }, {
                            'h4 m-3': !isMobile
                        })}>
                            <NavLink id='selector' className=' text-white' to={val.to}>{val.alt}</NavLink>
                        </div>
                    </div>)}
                </div>
            </div>

        )
    }
}
const mapStateToProps = (state, ownProps) => ({Info: getServiceInfo(state)})

export default connect(mapStateToProps)(ServiceMain)


