import React, {Component} from 'react'
import PropTypes from 'prop-types'

import qotes from '../../images/qotes.webp'
import './Rewiews.css'

export default class Rewiew extends Component {
    render() {
        const {link, who, rewiew, auto, workType} = this.props.val
        return (
            <div className='col media p-3'>
                <img src={qotes} className='align-self-start mr-3' alt=''></img>
                <div className='media body rewiew'>
                    <h5>
                        <em>
                            {auto} <span className='kosoy'>|</span> {workType}
                        </em>
                    </h5>
                    <p className='mt-2 '>{rewiew}</p>
                    <p className='who w-100 h-auto'>
                        <a href={link}>
                            <strong>
                                <em>
                                    <u >{who}</u>
                                </em>
                            </strong>
                        </a>
                    </p>
                </div>
            </div>
        )
    }
}

Rewiew.propType = {
    who: PropTypes.string.isRequired,
    auto: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    rewiew: PropTypes.string.isRequired,
    workType: PropTypes.string.isRequired
}


