import React from 'react'
import './BlackBlock.css'

export default({

    ...res
}) => {
    return (
        <div
            className='d-flex align-items-end p-2 h-100  black-back justify-content-center '>
            <div className='h2 text-white' {...res}></div>
        </div>

    )
}



